@import url(https://fonts.googleapis.com/css2?family=Poppins);
body{background-color:#E5F2F4;font-family:'Poppins', sans-serif}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.benefits-space{margin-left:10%;margin-right:10%;margin-top:5%}.benefits-space .benefits-space__title{font-size:48px;text-align:center;margin-bottom:0px}.benefits-space .benefits-space__imge{width:100%;height:auto}.benefits-cards__card--small{height:96px;width:100px;border-top-left-radius:20px;border-bottom-right-radius:20px;background-color:#09f;margin-right:15px;margin-top:33px;padding:10px}.benefits-cards__card--large{background:rgba(208,187,187,0.25);border-radius:15px;margin-top:33px;padding-top:10px;margin-right:15px}.benefits-cards__imge{width:100%;height:auto}.benefits-cards__title{font-weight:bold}

.footer{position:absolute;margin-top:15%;color:antiquewhite;font-size:15px;display:flex;justify-content:center}.footer .footer_text{margin:0px}.footer .footer__text--under{margin-top:5%}.footer .footer__containerIcons{margin-left:25%}@media (max-width: 770px){.footer{position:absolute;margin-top:12%;font-size:10px;margin-left:5%}}@media (max-width: 540px){.footer{position:absolute;margin-top:11%;font-size:7px;margin-left:3%}}@media (max-width: 415px){.footer__containerIcons{display:none}}@media (max-width: 360px){.footer{font-size:0px}}

.navbar{background-color:#0567fc;padding-right:2%}.navbar .navbar__logo{color:azure;text-decoration:none}.navbar .navbar__link{color:azure;margin-left:10px;text-decoration:none}

.contact_info{text-align:start;background-color:#09f;padding:30px;border:50px}.buttons{text-align:end}.buttons__send{background-color:#03419F;color:azure;justify-content:end}.info_business{text-align:start}.info_location{border-radius:15px}.info_business--marker_icon,.info_business--phone_icon,.info_business--envelope_icon{color:#03419F}

.aboutUs{text-align:center}.about_us__info{background-image:url(/alonsoalcuadrado/static/media/Vector.fbb6958f.png);position:relative;text-align:left;background-repeat:round;width:100vw;top:25px}@media (max-width: 850px){.about_us__info{background-repeat:no-repeat;width:100%}}@media (max-width: 550px){.about_us__info{width:100vw}}@media (max-width: 370px){.about_us__info{width:100%}}.about_us__info--data{padding:40px;text-align:center}.about_us__info--image{bottom:-5px;text-align:center}@media (max-width: 831px){.about_us__info--image{width:50vw;justify-content:center}}@media (max-width: 550px){.about_us__info--image{width:70vw}}@media (max-width: 375px){.about_us__info--image{width:75vw}}

.service-cards{margin-left:15%;margin-right:15%}.service-cards .service-cards__title{font-size:48px;line-height:72px;text-align:center;margin-top:50px;margin-bottom:0px}.service-card{height:360px;border-radius:10px;filter:drop-shadow(0px 4px 4px rgba(0,0,0,0.25)) drop-shadow(0px 4px 4px rgba(0,0,0,0.25));margin-top:20px}.service-card .service-card__card-header{background:rgba(228,228,228,0.5);height:90px;text-align:center;padding-top:10px}.service-card .service-card__card-text{height:180px;text-align:left}.service-card .service-card__button{margin-left:80px;margin-right:80px;text-align:center;padding:5px 30px;background:#09f;border-radius:10px;color:ivory}

.services{margin-left:8%;margin-right:8%;margin-top:2%}.services .services__title{font-size:48px;text-align:center;margin-bottom:30px}.services .services__row{margin-left:0px;margin-right:0px}.card-information{color:aliceblue;margin-bottom:5%}.card-information .card-information__card{border-top-left-radius:20px;border-bottom-right-radius:20px;background-color:#0099FF;padding:10px}.card-information .card-information__img{width:80%;height:auto}.card-information .card-information__title{text-align:center;font-size:110%}.card-information .card-information__points{text-align:left}

